                    <div class="form-control-flush">
                      <div
                        class="tk-layout-inline-block tk-layout-inline-block-top tk-font-color-white text-xxl lh-140"
                        style="width: 75%"
                      >
                        Hi student, Welcome to<br />{{ studioName }}!
                      </div>
                      <div
                        class="avatar rounded-circle tk-layout-inline-block tk-layout-inline-block-top tk-bg-color-transparent"
                        style="width: 25%"
                      >
                        <!-- <img :src="logo" /> -->
                        <avatar
                          :studioId="studioInfo.id"
                          :name="studioInfo.name"
                        ></avatar>
