                    </div>
                  </div>
                </span>

                <div>
                  <div
                    class="tk-font-color-white tk-font-weight-light"
                    style="opacity: 0.8; margin-bottom: 0.5rem"
                  >
                    Your lesson:
                  </div>
                  <div class="card mb-4">
                    <div class="card-body">
                      <div
                        class="tk-lesson-type-instrument tk-layout-inline-block"
                      >
                        <div class="avatar rounded-circle tk-bg-color-white">
                          <img :src="lessonTypeInstrumentIcon" />
                        </div>
                        <div class="tk-lesson-type-check">
                          <i class="fas fa-check-circle"></i>
                        </div>
                      </div>
                      <div
                        class="tk-layout-inline-block"
                        style="width: calc(100% - 80px); margin-left: 16px"
                      >
                        <div
                          class="tk-font-weight-bold tk-font-letter-spacing-1 tk-font-color-black2 text-lg"
                        >
                          Trial lesson
                        </div>
                        <div>
                          <div
                            class="tk-px-width-px24 tk-px-height-px24 tk-layout-inline-block"
                            style="border-radius: 40px; overflow: hidden"
                          >
                            <!-- <img
                              :src="logo"
                              style="width: 100%;vertical-align:top"
                            /> -->
                            <avatar
                              :studioId="studioInfo.id"
                              :name="studioInfo.name"
                              sizeClass="avatar-xs"
                              textClass="text-xs"
                              style="vertical-align: top"
                            ></avatar>
                          </div>
                          <div
                            class="tk-layout-inline-block tk-font-color-gray tk-font-weight-light text-sm"
                            style="width: calc(100% - 32px); margin-left: 8px"
                          >
                            {{ name }}
                          </div>
                        </div>
                        <div
                          class="tk-font-weight-light tk-font-color-gray text-sm"
                          style="margin-top: 4px"
                        >
