                          line-height: 40px;
                        "
                      >
                        <i class="fas fa-phone"></i>
                      </div>
                    </div>
                  </div>
                  <div class="card mb-4" v-if="email !== ''">
                    <div class="card-body">
                      <div
                        class="tk-layout-inline-block"
                        style="width: calc(100% - 48px)"
                      >
                        <div
                          class="tk-font-weight-bold tk-font-letter-spacing-1 tk-font-color-black2 text-lg"
                        >
                          {{ email }}
                        </div>

                        <div
                          class="tk-font-weight-light tk-font-color-gray text-sm"
                          style="margin-top: 2px"
                        >
                          Email
                        </div>
                      </div>
                      <div
                        class="tk-layout-inline-block tk-px-width-px40 tk-bg-color-orange tk-font-color-white tk-text-center"
                        style="
                          margint-left: 8px;
