</template>

<script>
import { Select, Option } from "element-ui";
import userService from "@/services/userService";
import teacherService from "@/services/teacherService";
import collections from "@/services/collections";
import commons from "@/util/commons";
import query from "@/services/query";
import ImgCutter from "vue-img-cutter";
import $ from "jquery";
import "firebase/storage";
import bus from "@/bus.js"
export default {
  components: {
    DropzoneFileUpload: () => import("@/components/Inputs/DropzoneFileUpload"),
    [Select.name]: Select,
    [Option.name]: Option,
    ImgCutter,
    loading: () => import("@/tkViews/components/layout/Loading"),
    avatar: () => import("@/tkViews/components/layout/Avatar"),
    cardLoading: () => import("@/tkViews/components/layout/CardLoading"),
    buttonWithLoading: () =>
      import("@/tkViews/components/layout/ButtonWithLoading"),
  },
  data() {
    return {
      boxSize: 0,
      lessonTypeInstrumentIcon: "",
      logo: "",
      crop: "",
      newLogo: "",
      isClear: false,

      loading: true,

      studioName: "",
      name: "",
      email: "",
      phone: "",
      street: "",
      city: "",
      zipCode: "",
      stateSelected: " ",
      stateSelects: {},
      isInvalid: false,
      countryCodeSelected: " ",
      countrySelects: {},
      model: {
        title: "",
        className: "tk-bg-color-white",
        start: "",
        end: "",
      },
      eventColors: [
        "tk-bg-color-main",
        "tk-bg-color-lightGreyBlue",
        "tk-bg-color-salmon",
        "tk-bg-color-yellowOrange",
      ],
      inputs: {
        tags: ["BUCHAREST", "IASI", "TIMISOARA"],
        fileSingle: [],
        fileMultiple: [],
      },

      studioInfo: {},
      teacherInfo: {},
      userInfo: {},
      isButtonLoadingComplete: false,
    };
  },
  watch: {
    countryCodeSelected(val, old) {
      let self = this;
      if (!self.loading) {
        self.resetPhonePrefix(val);
      }
    },
    stateSelected(val, old) {
      let self = this;
      if (!self.loading) {
        if (self.$tools.isNull(val)) {
          self.stateSelected = " ";
        }
        self.resetStateCode(val);
      }
    },
    street(val, old) {
      if (this.$tools.isNull(val)) {
        this.street = "";
      }
    },
    city(val, old) {
      if (this.$tools.isNull(val)) {
        this.city = "";
      } else {
        let city = val.toLowerCase();
        this.stateSelects.options.some((item) => {
          if (item.label.toLowerCase() === city) {
            this.stateSelected = item.value;
          }
        });
      }
    },
    zipCode(val, old) {
      if (this.$tools.isNull(val)) {
        this.zipCode = "";
      }
    },
    loading(val, old) {
      if (!val) {
        setTimeout(function () {
          // document.getElementById("uploadLogo").childNodes[1].innerText =
          //   "Drop logo here to upload";
          // document.getElementById("uploadLogo").childNodes[1].style.height =
          //   "210px";
          // document.getElementById("uploadLogo").childNodes[1].style.lineHeight =
          //   "56px";
          document.getElementById("storefrontColor").style.marginTop = "16px";
        }, 1000);

        console.log("reset");
        this.resetPhonePrefix(this.countryCodeSelected);
        this.resetStateCode(this.stateSelected);
      }
    },
  },
  async mounted() {
    let self = this;
    self.countrySelects = self.$tools.countrySelects;
    self.stateSelects = self.$tools.stateSelects;

    let studioInfo = await userService.studioInfo();
    let teacherInfo = await userService.teacherInfo();
    let userInfo = await userService.userInfo();

    self.loading = false;
    console.log("not loading");

    console.log("studio: ", studioInfo);
    console.log("teacherInfo: ", teacherInfo);
    console.log("userInfo: ", userInfo);
    self.studioInfo = studioInfo;
    self.teacherInfo = teacherInfo;
    self.userInfo = userInfo;

    self.studioName = studioInfo.name;
    self.name = userInfo.name;
    self.email = userInfo.email;
    self.phone = userInfo.phone;
    self.street = userInfo.street;
    self.city = userInfo.city;
    self.zipCode = userInfo.zipCode;

    let storefrontColor = studioInfo.storefrontColor;
    switch (storefrontColor.toLowerCase()) {
      case "71d9c2":
        self.model.className = "tk-bg-color-main";
        break;
      case "97bddd":
        self.model.className = "tk-bg-color-lightGreyBlue";
        break;
      case "f38577":
        self.model.className = "tk-bg-color-salmon";
        break;
      case "f6af02":
        self.model.className = "tk-bg-color-yellowOrange";
        break;
    }

    self.logo = commons.studioLogoPath(studioInfo.id);

    let lessonTypes = query.getLessonTypesByTeacherId(userInfo.userId);
    let instruments = self.$tools.instruments;
    if (lessonTypes.length > 0) {
      instruments.some((item) => {
        if (parseInt(item.id) === parseInt(lessonTypes[0].instrumentId)) {
          self.lessonTypeInstrumentIcon = item.url;
        }
      });
    } else {
      self.lessonTypeInstrumentIcon = instruments[0].url;
    }

    setTimeout(() => {
      console.log(
        "-=-=-=-=-=-=",
        document.getElementsByClassName("tk-select-border-none").length
      );

      if (
        !self.$tools.isNull(userInfo.phonePrefix) ||
        userInfo.phonePrefix === ""
      ) {
        self.countryCodeSelected = userInfo.phonePrefix;
        self.resetPhonePrefix(userInfo.phonePrefix);
      }
      if (!self.$tools.isNull(userInfo.state) || userInfo.state === "") {
        self.stateSelected = userInfo.state;
        self.resetStateCode(userInfo.state);
      }

      self.boxSize = $("#logoContainer").width();
    }, 200);

    $("#logoContainer").resize(function (e) {
      console.log(e);
      self.boxSize = $("#logoContainer").width() + 1.5 * 16;
    });
  },
  methods: {
    resetPhonePrefix(val) {
      let self = this;
      if (
        !self.$tools.isNull(
          document.getElementsByClassName("tk-select-border-none")[0]
        ) &&
        !self.$tools.isNull(
          document.getElementsByClassName("tk-select-border-none")[0]
            .childNodes[1]
        )
      ) {
        document.getElementsByClassName(
          "tk-select-border-none"
        )[0].childNodes[1].childNodes[1].style.display = "none";
        self.countrySelects.options.some((item) => {
          if (item.value === val) {
            setTimeout(() => {
              document.getElementsByClassName(
                "tk-select-border-none"
              )[0].childNodes[1].childNodes[1].value = item.areaCode;
              document.getElementsByClassName(
                "tk-select-border-none"
              )[0].childNodes[1].childNodes[1].style.display = "block";
            }, 10);
          }
        });
      }
    },
    resetStateCode(val) {
      let self = this;
      if (
        !self.$tools.isNull(
          document.getElementsByClassName("tk-select-border-none")[1]
        ) &&
        !self.$tools.isNull(
          document.getElementsByClassName("tk-select-border-none")[1]
            .childNodes[1]
        )
      ) {
        document.getElementsByClassName(
          "tk-select-border-none"
        )[1].childNodes[1].childNodes[1].style.display = "none";
        self.stateSelects.options.some((item) => {
          if (item.value === val) {
            setTimeout(() => {
              document.getElementsByClassName(
                "tk-select-border-none"
              )[1].childNodes[1].childNodes[1].value = item.value;
              document.getElementsByClassName(
                "tk-select-border-none"
              )[1].childNodes[1].childNodes[1].style.display = "block";
            }, 10);
          }
        });
      }
    },
    getPhonePrefix() {
      let self = this;
      let prefix;
      self.countrySelects.options.some((item) => {
        if (item.value === self.countryCodeSelected) {
          prefix = item.areaCode;
        }
      });
      return prefix;
    },
    validationEmail() {
      let self = this;
      if (self.$tools.isNull(self.email)) {
        self.isInvalid = false;
      } else {
        self.isInvalid = !self.$tools.isEmail(self.email);
      }
    },
    async saveStudioInfo() {
      let self = this;

      console.log({
        name: self.studioName,
        storefrontColor: self.getStoreFrontColor(),
        email: self.email,
        phone: self.phone,
        street: self.street,
        city: self.city,
        zipCode: self.zipCode,
        state: self.stateSelected == " " ? "" : self.stateSelected,
        phonePrefix:
          self.countryCodeSelected == " " ? "" : self.countryCodeSelected,
      });

      await collections.studio.doc(self.studioInfo.id).update({
        name: self.studioName,
        storefrontColor: self.getStoreFrontColor(),
        updateTime: parseInt(new Date().getTime() / 1000).toString(),
      });

      if (!self.$tools.isNull(self.newLogo)) {
        let storage = self.$firebase.storage();
        let storageRef = storage.ref();
        let spaceRef = storageRef.child(
          "images/studio_logos/" + self.studioInfo.id + ".jpg"
        );
        spaceRef.put(self.newLogo.blob).then((snapshot) => {
          bus.$emit("refreshAvatar")
        });
      }

      await collections.user
        .doc(self.userInfo.userId)
        .update({
          email: self.email,
          phone: self.phone,
          street: self.street,
          city: self.city,
          zipCode: self.zipCode,
          state: self.stateSelected == " " ? "" : self.stateSelected,
          phonePrefix:
            self.countryCodeSelected == "" ? "" : self.countryCodeSelected,
          updateTime: parseInt(new Date().getTime() / 1000).toString(),
        })
        .then(() => {
          self.isButtonLoadingComplete = true;
          self.$notify({
            message: "Update Success!",
            type: "success",
          });
        })
        .catch((err) => {
          self.$notify({
            message: "Update failed, try it again!",
            type: "error",
          });
          console.log("update failed, error: ", err);
        });

      self.isButtonLoadingComplete = false;
    },
    getStoreFrontColor() {
      let self = this;
      switch (self.model.className) {
        case "tk-bg-color-main":
          return "71d9c2";
        case "tk-bg-color-lightGreyBlue":
          return "97bddd";
        case "tk-bg-color-salmon":
          return "f38577";
        case "tk-bg-color-yellowOrange":
          return "f6af02";
      }
    },
    openCutter() {
      console.log("open");
      setTimeout(() => {
        document.getElementsByClassName("tool-title")[0].style.width = "100%";
        document.getElementsByClassName("tool-title")[0].style.height = "20px";
        document.getElementsByClassName("closeIcon")[0].style.backgroundColor =
          "#b6bec5";
        document.getElementsByClassName("mask")[0].style.zIndex = 9999;
        document.getElementsByClassName("mask")[0].style.width = "100vw";
        document.getElementsByClassName("mask")[0].style.height = "100vh";
      }, 100);
    },
    onCutDown(data) {
      let self = this;
      console.log("onCutDown", data);
      self.isClear = true;
      self.logo = data.dataURL;
      self.newLogo = data;
    },
    onCutError(data) {
      let self = this;
      console.log("onCutError", data);
    },
    onChooseImg(data) {
      let self = this;
      console.log("onChooseImg", data);
      self.isClear = false;
    },
    onPrintImg(data) {
      let self = this;
      console.log("onPrintImg", data);
      if (self.isClear) {
        self.crop = "";
      } else {
        self.crop = data.dataURL;
      }
    },
    onClearAll(data) {
      let self = this;
      console.log("onClearAll", data);
      self.crop = ""
      self.isClear = true;
    },
  },
};
</script>
